*{
  margin: 0%;
  padding:0%;
  box-sizing: border-box;
  
}

.main-background{
  background-position: center;
  background-size: cover;
  height: 120vh;
  width: 100%;
}
.header{
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding-top: .3rem;
  font-family: 'Poppins', sans-serif;
  font-weight: 700;
  font-size: 1.7rem;
  align-items: center;
}
.header-logo{
  padding-left: 1rem;
  font-family: 'Poppins', sans-serif;
  font-weight: 700;
  font-size: 1.8rem;
  color: white;
}
.location-info{
  padding-left: 1.4rem;
  width:40%;
  border-radius: 15rem;
  border: none;
  background-color: rgba(240, 248, 255, 0.745);
  height: 2.5rem;
  font-family: 'Poppins', sans-serif;
  font-weight: 700;
  margin-left: 1rem;
  margin-right: 1rem;
  background-repeat: no-repeat;
  background-size: 2.5rem;
}
.search-input{
  font-family: 'Poppins', sans-serif;
  font-weight: 700;
  border-radius: 2rem;
  width: 5rem;
  color: rgb(129, 129, 129);
  height: 2.8rem;
  border: none;
  background-color: rgba(240, 248, 255, 0.694);
  margin-right: 1rem;
}

.weather-text{
  width: 80%;
  margin-left: 10%;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  margin-top: 3rem;
  height: 7rem;
  align-items: center;

  font-family: 'Poppins', sans-serif;
  font-weight: 700;
  font-size: 1.7rem;
 color: white;
}
.image-weather{
  margin-bottom: 1rem;
  width: 40%;
  
}
.info-container{
  
  width: 30%;
  color: rgb(199, 199, 199);
  font-weight: 500;
  color: aliceblue;
}
.more-info{
  display: inline-block;
  width: 4rem;
  height: 2rem;
  font-weight: 500;
  color: rgb(7, 7, 7);
  font-family: 'Poppins', sans-serif;
  
  border-radius: 3rem;
  border: .2rem solid rgba(240, 248, 255, 0.457);
  margin-top: 1rem;
  font-size: 1.1rem;
  background-color: rgba(149, 84, 209, 0.849);

}
.suggestion{
  display: inline-block;
  width: 9rem;
  height: 2.3rem;
  font-weight: 500;
  color: rgb(7, 7, 7);
  font-family: 'Poppins', sans-serif;
  
  border-radius: 3rem;
  border: .2rem solid rgba(240, 248, 255, 0.457);
  margin-top: 1rem;
  font-size: 1.1rem;
  background-color: rgba(149, 84, 209, 0.849);

}
.btn-container{
  display: block;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  
  margin-top: 1rem;
}
.image-temp{
  width: 80%;
    margin-left: 10%;
  height: 8rem;
  display: flex;
  justify-content: space-between;
  align-self:center;
}
.temperature{
  font-size: 2.6rem;
}
.name{
  margin-top: .5rem;
  font-size: 2.8rem;
}
.weather-des{
  
font-size: 2rem;
}
.weather-card-false{
  width: 90%;
  margin-left: 5%;
  height:13rem ;
  background-color: rgba(240, 255, 255, 0.404);
  border-radius: 3rem;  
  transition: .2s;
}
.weather-card-true{
  width: 90%;
  margin-left: 5%;
  height:27rem ;
  background-color: rgba(240, 255, 255, 0.404);
  border-radius: 3rem;
  transition: .01s;  
}

.data-true{
  margin-top: 1rem;
  width: 90%;
  margin-left: 5%;
  height:12rem ;
  background-color: rgba(240, 255, 255, 0.404);
  border-radius: 3rem;  
  transition: .2s all;
}
.data-false{
  height: 0rem;
  transition: .2s all;
}
.data-true li{
  width: 90%;
  margin-left: 5%;
  padding-top: 1rem;
  text-decoration: none;
  color: aliceblue;
  list-style: none;
  font-size: 1rem;
  font-weight: 500;
  font-family: 'Poppins', sans-serif;
}


.extras{
  width: 100%;
  margin-left: 25%;
  display: flex;
  margin-top: 1rem;
  flex-direction: column;

  justify-content: space-around;
  height: 12rem;
}
.extras h1{
  color: aliceblue;
  font-size: 2rem;

}

.extras h1:active
{
  letter-spacing: .1rem;
}

@media (max-width: 550px) {
  .main-background{
    height: 160vh;
    background-size: cover;
  }
  .location-info{
    height: 1.9rem;
    padding-left: 1.1rem;
    font-size: .6rem;
  }
  .weather-text{
    width: 90%;
   margin-left: 5%;
    font-size: 1rem;
    margin-top: 1rem;
  }
  .temperature{
  font-size: 1.6rem;
}
.name{
  margin-top: .5rem;
  font-size: 1.6rem;
}
.weather-des{
  
font-size: 1.3rem;
}
.extras{
  display: block;
  

  
 }
}

.image{
  width: 80%;
  height: 170%;
}



@media (max-width:550px) {
  .image{
    width: 110%;
    
  }
  .info-container{
width: 40%;
  }
  .header-logo{
    font-size: 1.5rem;
  }
  .location-info{
    height: 1.9rem;
    padding-left: .3rem;
    font-size: .6rem;
  }
  .search-input{
    height: 2rem;
    font-size: .8rem;
    border: .1rem groove orangered;

  }
  .extras h1{
    font-size: 1.5rem;
  }
}
